.intro {
  padding-top: 190px;
  padding-bottom: 150px;

  .intro-content {
    width: 100%;
    height: 400px;
    background-color: rgba(201, 156, 156, 0.3);
  }

  .site-section__main-content {
    padding-bottom: 70px;
  }

  img {
    height: 450px;
  }

  h1 {
    font-weight: bold;
    font-size: 36px;
  }

  p {
    font-size: 18px;
  }

  span {
    color: @primary-color;
  }
}

.intro__form {
  input {
    min-width: 280px;
    padding: 0.65rem 1rem;
    margin-bottom: 10px;
  }

  button {
    padding: 10px 16px;
    background-color: @primary-color;
    color: #ffffff;
    font-weight: bold;
    transition: all 1s ease-in-out;

    &:hover {
      color: @primary-color;
      background-color: #ffffff;
      border: 1px solid @primary-color;
    }
  }

  span {
    max-width: 296px;
    position: relative;
    margin-left: 20px;
    margin-top: 0;
    margin-bottom: 5px;

    &::before {
      content: "";
      position: absolute;
      top: 5.5px;
      left: -11px;
      width: 15px;
      height: 15px;
      background-image: url("../img/control-mark.svg");
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

.intro__footer {
  display: flex;
  flex-direction: column;
  &__buttons {
    margin-bottom: 20px;
    button, a {
      padding: 10px 16px;
      background-color: @primary-color;
      color: #ffffff;
      font-weight: bold;
      transition: all 1s ease-in-out;

      &:hover {
        color: @primary-color;
        background-color: #ffffff;
        border: 1px solid @primary-color;
      }
    }
  }

  &__possibilities {
    display: flex;
    flex-direction: column;
    span {
      max-width: 296px;
      position: relative;
      margin-left: 20px;
      margin-top: 0;
      margin-bottom: 5px;

      &::before {
        content: "";
        position: absolute;
        top: 5.5px;
        left: -20px;
        width: 15px;
        height: 15px;
        background-image: url("../img/control-mark.svg");
        background-repeat: no-repeat;
        background-size: contain;
      }
    }
  }

  &--default {
    display: flex !important;
  }

  &--mobile {
    display: none !important;
  }
}

