.intro ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 10px;

  li {
    padding: 20px;
    border-radius: 16px;
    border: 1px solid #e3e8fc;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .intro__advantages {
    font-weight: bold;
    font-size: 36px;
    line-height: 100%;
    color: @primary-color;
    // margin-right: 15px;
    margin-bottom: 5px;
    text-align: center;

    span {
      font-size: 18px;
    }
  }

  p {
    text-align: center;
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 0;
  }
}

@media (max-width: 1399px) {
  .intro {
    padding-top: 120px;
    padding-bottom: 120px;

    .site-section__main-content {
      padding-bottom: 50px;

      h1, p {
        text-align: center;
      }

      .intro__footer {
        display: flex;
        flex-direction: column;
        align-items: center;

        &--default {
          display: none !important;
        }

        &--mobile {
          margin-top: 40px;
          display: flex !important;
        }
      }
    }

    img {
      display: block;
      margin: 0 auto;
      border: 1px solid #dadada;
      padding: 10px;
      border-radius: 8px;
      width: 600px;
      height: auto;
    }

    ul {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 10px;
      row-gap: 10px;

      li {
        padding: 20px;
        border-radius: 16px;
        border: 1px solid #e3e8fc;
        background-color: #ffffff;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }

      .intro__advantages {
        font-weight: bold;
        font-size: 36px;
        line-height: 100%;
        color: @primary-color;
        // margin-right: 15px;
        margin-bottom: 5px;
        text-align: center;

        span {
          font-size: 18px;
        }
      }

      p {
        text-align: center;
        font-weight: bold;
        font-size: 16px;
        margin-bottom: 0;
      }
    }
  }
}

@media (max-width: 991px) {
  .intro {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}

@media (max-width: 767px) {
  .intro {
    img {
      // border: none;
      padding: 10px;
      height: auto;
      width: 100%;
    }

    h1 {
      font-weight: bold;
      font-size: 24px;
    }

    p {
      font-size: 18px;
      margin-bottom: 0;
    }
  }

  .intro__footer {
    &__buttons {
      button, a {
        padding: 10px 16px;
        background-color: @primary-color;
        color: #ffffff;
        font-weight: bold;
        transition: all 1s ease-in-out;

        &:hover {
          color: @primary-color;
          background-color: #ffffff;
          border: 1px solid @primary-color;
        }
      }
    }

    &--mobile {
      margin-top: 20px !important;
      button, a {
        width: 100%;
        margin-bottom: 10px;
      }

      a {
        margin-bottom: 0;
      }
    }
  }

  .intro ul {
    display: grid;
    grid-template-columns: 1fr;
    column-gap: 10px;

    li {
      padding: 10px;
    }

    .intro__advantages {
      font-size: 24px;
      margin-bottom: 5px;
    }

    p {
      font-size: 16px;
    }
  }
}
