.main-footer {
  padding-top: 0;
  background: radial-gradient(98.32% 89.32% at 50% -31.76%, #4d5ea5 0, #272d45 100%);

  &__nav {
    border-bottom: 1px solid #ffffff;
    padding-bottom: 60px;
  }

  p {
    color: #ffffff;
  }

  h3 {
    color: #ffffff;
  }
}

.main-footer ul {
  li {
    text-align: left;

    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  li > a {
    color: #ffffff;
  }
}

.main-footer__social {
  color: #ffffff;
  padding: 40px 0;

  p {
    margin: 0;
    font-size: 14px;
  }

  ul {
    display: flex;
    margin-bottom: 0;
  }

  ul li {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  ul a {
    display: flex;
    align-items: center;
    img {
      margin-right: 5px;
    }
    span {
      text-decoration: underline;
      font-size: 18px;
    }
  }
}

.main-footer a {
  transition: all 0.6s ease-in-out;

  &:hover {
    transform: scale(1.05);
    opacity: 0.6;
  }
}

@media (max-width: 1439px) {
  .main-footer__social {
    padding: 20px 0;

    ul a {
      span {
        text-decoration: underline;
        font-size: 16px;
      }
    }
  }
}

@media (max-width: 767px) {
  .main-footer {
    p {
      text-align: center;
      font-size: 12px;
      margin-bottom: 10px;
    }

    ul {
      li {
        a {
          img {
            margin-top: 3px;
            width: 18px;
            height: 18px;
          }

          span {
            font-size: 14px;
          }
        }
      }
    }
  }
}
