.gallery {
  padding-top: 80px;
  padding-bottom: 80px;

  .carousel {
    ul {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      li {
        display: flex;
        align-items: center;
        height: 130px;

        img {
          width: 130px;
          min-width: 130px;
          max-width: 130px;
          height: auto;
        }
      }
    }

    .carousel-control-prev,
    .carousel-control-next {
      display: none;
    }
  }

  @media (max-width: 1439px) {
    h2 {
      font-size: 30px;
    }
    .carousel {
      ul {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        li {
          display: flex;
          align-items: center;
          height: 85px;
          img {
            width: 80px;
            min-width: 80px;
            max-width: 80px;
            height: auto;
          }
        }
      }

      .carousel-control-prev,
      .carousel-control-next {
        display: none;
      }
    }
  }

  @media (max-width: 767px) {
    padding: 60px 0;

    h2 {
      font-size: 24px;
      margin-bottom: 20px;
    }

    .carousel {
      ul {
        li {
          display: flex;
          align-items: center;
          height: 65px;

          img {
            width: 50px;
            max-width: 50px;
            min-width: 50px;
            height: auto;
          }
        }
      }
    }
  }
}
