.faq {
  @media (max-width: 1439px) {
    .section-heading {
      margin-bottom: 30px;
    }
    &__row {
      .faq__column {
        &--accordion {
          order: 2;
        }
        &--title {
          order: 1;
        }
      }
    }
  }

  @media (max-width: 767px) {
    .accordion {
      margin-bottom: 30px;

      .accordion-item {
        h2 button {
          font-size: 16px;
        }

        .accordion-body {
          font-size: 14px;
        }
      }
    }
  }
}
