.stages {
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 35px;
    margin-bottom: 53px;
    li {
      background-color: #ffffff;
      padding: 40px;
      border-radius: 8px;

      span {
        box-sizing: border-box;
        width: 60px;
        height: 60px;
        display: block;
        margin-bottom: 16px;
        color: #ffffff;
        font-size: 25px;
        font-weight: bold;
        background-color: #316AFF;
        padding: 10px 23px;
        border-radius: 8px;
      }

      h3 {
        font-size: 25px;
        font-weight: bold;
        margin-bottom: 16px;
      }

      p {
        font-size: 16px;
      }
    }
  }
  button, a {
    max-width: 200px;
    padding: 10px 16px;
    background-color: @primary-color;
    color: #ffffff;
    font-weight: bold;
    transition: all 1s ease-in-out;

    &:hover {
      color: @primary-color;
      background-color: #ffffff;
      border: 1px solid @primary-color;
    }
  }

  @media (max-width: 1439px) {
    ul {
      grid-template-columns: 1fr;
      gap: 20px;

      li {
        padding: 20px;
      }
    }
    button {
      max-width: 50%;
    }
  }

  @media (max-width: 767px) {
    h2 {
      max-width: 100%;
      font-size: 24px;
    }
    ul {
      padding-right: var(--bs-gutter-x, 0.75rem);
      padding-left: var(--bs-gutter-x, 0.75rem);
      margin-bottom: 10px;
      li {
        padding: 10px;
        text-align: center;
        h3 {
          font-size: 20px;
          margin-bottom: 10px;
        }
        span {
          width: 40px;
          height: 40px;
          font-size: 18px;
          padding: 7px;
          margin: 0 auto;
          margin-bottom: 5px;
        }
        p {
          font-size: 14px;
        }
      }
    }

    button {
      max-width: 85%;
    }
  }
}
