.site-section {
  padding: 120px 0;

  h2 {
    font-size: 34px;
    font-weight: bold;
  }

  p {
    font-size: 18px;
  }

  &__main-content {
    padding-bottom: 120px;
  }

  @media (max-width: 1439px) {
    padding: 60px 0;
  }

  @media (max-width: 767px) {
    padding: 40px 0;

    h2 {
      font-size: 24px;
    }

    p {
      font-size: 14px;
    }
  }
}
