.banner {
  padding-top: 0;
  margin-top: -88px;
}

.banner__section {
  padding: 40px;
  border-radius: 20px;
  background-color: @primary-color;
  color: #ffffff;

  p {
    font-size: 20px;
    margin-bottom: 10px;
  }

  a {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    color: @primary-color;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    transition: all 0.6s ease-in-out;

    &:hover {
      transform: scale(1.1);
    }
  }
}

.banner__button {
  display: flex;
  justify-content: center;
}

@media (max-width: 1439px) {
  .banner__section {
    padding: 20px;

    h2 {
      font-size: 22px;
    }

    p {
      font-size: 16px;
      margin-bottom: 10px;
    }

    a {
      padding: 15px;
      font-size: 14px;
    }
  }
}

@media (max-width: 767px) {
  .banner__section {
    padding: 30px;

    h2 {
      font-size: 18px;
    }

    p {
      font-size: 14px;
      margin-bottom: 20px;
    }

    a {
      width: 94%;
      padding: 6px 0;
      text-align: center;
    }
  }
}
